.app {
  max-width: 540px;
  margin: 50px auto;
  color: #656565;
  background: #f3f3f3; }
  .app__header {
    padding: 30px 0;
    border-bottom: 3px solid #ced4dd; }
  .app__title {
    margin: 0;
    font-size: 28px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    color: inherit; }
  .app__task-list {
    list-style: none;
    margin: 0;
    padding: 0; }
  .app__footer {
    padding: 40px 0 50px; }


.page {
  font-family: 'Varela Round', sans-serif;
  font-size: 1rem;
  line-height: 1;
  background: #21406f; }


.paper {
  position: relative;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.15); }
  .paper::before, .paper::after {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: #f3f3f3;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.25); }
  .paper::before {
    transform: rotate(-1deg) translate(-2px, 0); }
  .paper::after {
    transform: rotate(1deg) translate(2px, -2px); }


.radiogroup {
  display: flex;
  justify-content: center;
  align-items: baseline;
  font-size: 16px; }
  .radiogroup__label {
    margin-right: 5px;
    text-transform: uppercase;
    color: #adadad; }
  .radiogroup__radio {
    display: block;
    outline: 0;
    margin-left: .5rem;
    border: 1px solid #dadada;
    border-radius: 15px;
    padding: 0 1em;
    font-size: 16px;
    line-height: 30px;
    color: #999999;
    background: transparent;
    text-transform: uppercase;
    transition: background-color 200ms ease; }
  .radiogroup__radio:hover, .radiogroup__radio_checked {
    background: #dddddd; }


.task-form {
  padding-left: 102px;
  border-bottom: 2px solid #ced4dd; }
  .task-form__name {
    -webkit-appearance: none;
    outline: 0;
    border: 0;
    border-left: 1px solid #F9C8C9;
    padding: 25px 20px;
    box-sizing: border-box;
    width: 100%;
    height: 70px;
    font-size: 20px;
    background: none; }
  .task-form__name::placeholder {
    color: #a9a9a9; }
  .task-form__name:focus {
    background: #ffffff; }


.task {
  display: flex;
  align-items: center;
  border-bottom: 2px solid #ced4dd; }
  .task_completed {
    color: #999999;
    text-decoration: line-through; }
  .task__checkbox {
    -webkit-appearance: none;
    flex-shrink: 0;
    outline: 0;
    margin-right: 25px;
    margin-left: 45px;
    border: 1px solid #d3d3d3;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    background: #fafafa linear-gradient(#fafafa, #f0f0f0);
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
    cursor: pointer; }
  .task__checkbox:hover {
    background: #f8f8f8; }
  .task_completed .task__checkbox {
    background: linear-gradient(#eeeeee, #fafafa);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.15); }
    .task_completed .task__checkbox::before {
      content: '';
      display: block;
      box-sizing: border-box;
      border-radius: 50%;
      padding: 5px;
      width: 100%;
      height: 100%;
      background: linear-gradient(#6db836, #3e8734 90%);
      background-clip: content-box; }
  .task__name {
    border-left: 1px solid #F9C8C9;
    padding: 25px 20px;
    font-size: 20px;
    line-height: 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  .task__delete-button {
    position: relative;
    flex-shrink: 0;
    outline: 0;
    margin-right: 25px;
    margin-left: auto;
    border: 0;
    width: 26px;
    height: 26px;
    text-indent: -10000px;
    background: none;
    opacity: 0;
    transition: opacity 300ms ease, transform 200ms ease; }
    .task__delete-button::before, .task__delete-button::after {
      content: '';
      position: absolute;
      top: 0;
      left: 11px;
      background: #d62323;
      border-radius: 3px;
      width: 3px;
      height: 100%; }
    .task__delete-button::before {
      transform: rotate(45deg); }
    .task__delete-button::after {
      transform: rotate(-45deg); }
  .task__delete-button:hover {
    transform: scale(1.15); }
  .task:hover .task__delete-button, .task__delete-button:focus {
    opacity: 1; }

